
// Grid Settings Map

$susy: (
	columns: 12,
	math: fluid,
	global-box-sizing: border-box,
	container: 1200px,
	container-position: center
);

// Typography Setup

$sans-font-family: 'brandon-grotesque';
$serif-font-family: 'adobe-garamond-pro';

$ms-range:
  1.1   20em,
  1.4 60em;

$base-font-size: 16px;
$default-line-height: 1.5;

// Media Query Sizes

$desktop: 1024px;
$tablet: 768px;
$mobile-lg: 533px;
$mobile: 420px;
$mobile-sm: 320px;